import { Button, Form } from 'semantic-ui-react';
import { useDispatch, useSelector } from 'react-redux';
import { loguearse } from '../../../actions/authActions';
import { useFormik } from 'formik';
import * as Yup from "yup";

const FormLogin = () => {

  const dispatch = useDispatch();
  const { loading } = useSelector(state => state.auth);

  const formik = useFormik({
    initialValues: {
      email: '',
      password: ''
    },
    validationSchema: Yup.object({
      email: Yup.string().email('Ingrese un correo correcto').required('Este correo es requerido'),
      password: Yup.string().required('La contraseña es requerida.')
    }),
    onSubmit: (values) => {
      dispatch(loguearse(values));
    }
  })

  return ( 
      <Form className="form-login" onSubmit={formik.handleSubmit}>
        <Form.Group widths='equal'>
          <Form.Input 
            icon='user' 
            iconPosition='left' 
            label="Correo" 
            placeholder="correo"
            name="email"
            value={formik.values.email}
            onChange={formik.handleChange}
            error={formik.errors.email}
          />
        </Form.Group>
        <Form.Group widths='equal'>
          <Form.Input 
            icon='lock' 
            iconPosition='left' 
            label="Contraseña" 
            type="password" 
            placeholder="Contraseña"
            name="password"
            value={formik.values.password}
            onChange={formik.handleChange}
            
            // error={formik.errors.password}
          />
        </Form.Group>
        <Form.Group className="form-login__submit">
          <Button type="submit" primary loading={loading} disabled={loading}>
            Acceder
          </Button>
        </Form.Group>
      </Form>
  );
}

export default FormLogin;